//                     <p> </p>
//                     <div className='btns'>
//                         <div className='btn btn1'></div>
//                         <div className='btn btn2'></div>
//                     </div>
//                 </PopupBtn>
//             </PopupBox>
//         </>
//     )
// }

export default Main;
