import React, { useEffect, useState } from "react";
import {
  AddPlaceMidBox,
  Title,
  PlaceBox,
  ImgBox,
  PlaceName,
  SelectBtnBox,
  SelectBtn,
  nearPlace,
  NearPlaceBox,
  NearAttractionConatiner,
} from "./PlacePc.styled";
import { saveNearAttraction } from "../../redux/features/dataForGpt";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

const AddPlaceMid = ({ page, day, choiceIndex, setChoice, nearPlace }) => {
  const nav = useNavigate();
  const attractionsWithImg = useSelector((state) => {
    return state.attractionsWithImg;
  });
  // attraction     attractionsWithImg dispatch
  const attractionsWithImgDispatch = useDispatch();

  //  ,   gpt     **         **
  const [parentAttraction, setParentAttraction] = useState("");

  const noImg = "/imgs/icons/no-image.png";
  useEffect(() => {}, [attractionsWithImg]);

  const isChoice = (value) => {
    let temp = [];
    if (choiceIndex) {
      temp = choiceIndex.map((a) => {
        return a.name;
      });
    }

    if (temp.indexOf(value.name) !== -1) {
      let arr = choiceIndex.filter((va) => va.name !== value.name);
      setChoice(arr);
    } else {
      if (choiceIndex == "") {
        setChoice([value]);
      } else {
        setChoice([...choiceIndex, value]);
      }
    }
  };

  useEffect(() => {
    if (nearPlace !== undefined && nearPlace !== "") {
      if (nearPlace.length !== 0) {
        const tempNear = nearPlace.map((place) => {
          return {
            parentName: parentAttraction,
            name: place.name,
            detail: "",
            attractionLocation: {
              latitude: place.geometry.location.lat(),
              longitude: place.geometry.location.lng(),
            },
          };
        });
        attractionsWithImgDispatch(saveNearAttraction(tempNear));
      } else {
        alert("   .");
        nav("/");
      }
    }
  }, [nearPlace]);

  // let nearAttractions = [
  //   {
  //     name: "111",
  //     detail: "111",
  //     attractionLocation: {
  //       latitude: "35.6329",
  //       longitude: "139.8804",
  //     },
  //   },
  //   { name: "222" },
  //   { name: "333" },
  // ];

  return (
    <>
      <AddPlaceMidBox>
        <Title>day{day}  </Title>
        {choiceIndex !== "" &&
          attractionsWithImg.map((value, index) => {
            if (
              choiceIndex.some((ele) => {
                return ele.name === value.name;
              })
            ) {
              return (
                <>
                  <h3>AI</h3>
                  <PlaceBox key={index}>
                    <ImgBox>
                      <img
                        src={
                          attractionsWithImg[index].img.hits.length !== 0
                            ? attractionsWithImg[index].img.hits?.[0]
                                .largeImageURL
                            : noImg
                        }
                        alt=""
                      ></img>
                    </ImgBox>
                    <PlaceName>{value.name}</PlaceName>
                    <SelectBtnBox>
                      <SelectBtn
                        onClick={() => {
                          isChoice(value);
                        }}
                        back={"#277bc0"}
                        font={"white"}
                      >
                        
                      </SelectBtn>
                    </SelectBtnBox>
                  </PlaceBox>
                  <Title size={"14px"}> </Title>
                  <NearAttractionConatiner>
                    {attractionsWithImg[index]?.nearAttraction?.map(
                      (value2, index2) => {
                        let temp = [];
                        if (choiceIndex) {
                          temp = choiceIndex.map((a) => {
                            return a.name;
                          });
                        }
                        if (temp.indexOf(value2.name) == -1) {
                          return (
                            <>
                              <NearPlaceBox key={index2}>
                                <ImgBox>
                                  <img src={noImg} alt="" />
                                </ImgBox>
                                <PlaceName>{value2.name}</PlaceName>
                                <SelectBtnBox>
                                  <SelectBtn
                                    onClick={() => {
                                      isChoice(value2);
                                    }}
                                    back={"#edebeb"}
                                    font={"#9b9a9a"}
                                  >
                                    
                                  </SelectBtn>
                                </SelectBtnBox>
                              </NearPlaceBox>
                            </>
                          );
                        } else {
                          return (
                            <NearPlaceBox key={index2}>
                              <ImgBox>
                                <img src={noImg} alt="" />
                              </ImgBox>
                              <PlaceName>{value2.name}</PlaceName>
                              <SelectBtnBox>
                                <SelectBtn
                                  onClick={() => {
                                    isChoice(value2);
                                  }}
                                  back={"#277bc0"}
                                  font={"white"}
                                >
                                  
                                </SelectBtn>
                              </SelectBtnBox>
                            </NearPlaceBox>
                          );
                        }
                      }
                    )}
                  </NearAttractionConatiner>
                </>
              );
            } else {
              return (
                <>
                  <h3 style={{ marginTop: "20px", fontWeight: "bold" }}>
                    AI
                  </h3>
                  <PlaceBox key={index}>
                    <ImgBox>
                      <img
                        src={
                          attractionsWithImg[index].img.hits.length !== 0
                            ? attractionsWithImg[index].img.hits?.[0]
                                .largeImageURL
                            : noImg
                        }
                        alt=""
                      ></img>
                    </ImgBox>
                    <PlaceName>{value.name}</PlaceName>
                    <SelectBtnBox>
                      <SelectBtn
                        onClick={() => {
                          isChoice(value);
                          setParentAttraction(value.name);
                        }}
                        back={"#edebeb"}
                        font={"#9b9a9a"}
                      >
                        
                      </SelectBtn>
                    </SelectBtnBox>
                  </PlaceBox>
                </>
              );
            }
          })}
      </AddPlaceMidBox>
    </>
  );
};

export default AddPlaceMid;
