import React from 'react'
import { MoveBtn, MoveText, SignupBottomBox } from './Signup.styled'
import { useNavigate } from 'react-router-dom';

const SignupBottom = ({page}) => {
    const nav = useNavigate();

    let btn;
    let text;
    if(page == '') {
        btn = '  '
        text = '  ?'
    }else if(page == '') {
        btn = '  '
        text = '  ?'
    }
    
    const moveTo = () => {
        if(page == '') {
            nav('/login')
        }else if(page == '') {
            nav('/signup')
        }
    }

  return (
    <>
      <SignupBottomBox>
        <MoveText>{text}</MoveText>
        <MoveBtn onClick={moveTo}>{btn}</MoveBtn>
      </SignupBottomBox>
    </>
  )
}

export default SignupBottom
