import React, { useState } from "react";

import {
  MainTopBox,
  LogoBox,
  TextBox,
  InputBox,
  DateBoxWrap,
  DateBox,
} from "./MainPc.styled";



//  &  
const MainTopPc = ({ locationSearched }) => {
const logo2 = "/imgs/icons/logo2.png";
const mainText = "/imgs/icons/mainText.png";
const search = "/imgs/icons/search.png";
  const [location, setLocation] = useState();

  //     zerohoney
  const handleEnterKey = (event) => {
    if (event.key === "Enter") {
      locationSearched(location);
    }
  };
  return (
    <>
      <MainTopBox>
        <LogoBox>
          <img src={logo2}></img>
        </LogoBox>
        <TextBox>
          <img src={mainText}></img>
        </TextBox>
        <InputBox>
          <img
            onClick={() => {
              locationSearched(location);
            }}
            src={search}
          ></img>
          <input
            onChange={(e) => {
              setLocation(e.target.value);
            }}
            onKeyDown={handleEnterKey}
            type="text"
            placeholder=" ?"
          ></input>
        </InputBox>
      </MainTopBox>
    </>
  );
};

export default MainTopPc;
